import React from 'react';
import socketIOClient from "socket.io-client";
var socket;
const endpoint = 'http://localhost:8080/';
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data:[]
    };
    socket = socketIOClient(endpoint);
   
    
  }
  
  
  componentDidMount() {
    setInterval((data) => {
      fetch("https://www.zebapi.com/pro/v1/market")
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            data: [...this.state.data,result[40].buy]
            
          });
        }
        
        )
      }, 1000);
      
      
  }
render(){
  return (
    <div>
      {this.state.data.reverse().slice(0,20).map((item,index)=><h4 key={index}>{item}</h4>)}
    
    </div>
  );}
}

export default App;
